import React, { Component, Fragment } from 'react';
import {MDBProgress, MDBCol, MDBRow} from "mdbreact";
import "../Styles/dashboard.css";

class CardContent extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <Fragment>
                <MDBRow className="my-2">
                    <MDBCol>
                    <span className="float-left">{this.props.data.primary.title}</span>
                    <span className="float-right">
                    {this.props.data.primary.value}
                    </span>
                    </MDBCol>
                </MDBRow>
                <MDBRow className="my-2">
                    <MDBCol>
                    <MDBProgress material animated value={this.props.data.primary.progress_value} height="4px" />
                    </MDBCol>
                </MDBRow>
                <MDBRow className="my-1">
                    <MDBCol>
                    <small className="text-muted">
                    <span className="float-left">{this.props.data.primary.progress_title}</span>
                    <span className="float-right">
                    {`(${this.props.data.primary.progress_value}) %`}
                    </span>
                    </small>
                    </MDBCol>
                </MDBRow>
                <MDBRow className="my-2">
                    <MDBCol>
                    <span className="float-left">{this.props.data.secondary.title}</span>
                    <span className="float-right">
                    {this.props.data.secondary.value}
                    </span>
                    </MDBCol>
                </MDBRow>
                <MDBRow className="my-2">
                    <MDBCol>
                    <MDBProgress material animated value={this.props.data.secondary.progress_value} height="4px" />
                    </MDBCol>
                </MDBRow>
                <MDBRow className="my-1">
                    <MDBCol>
                    <small className="text-muted">
                    <span className="float-left">{this.props.data.secondary.progress_title}</span>
                    <span className="float-right">
                    {`(${this.props.data.secondary.progress_value}) %`}
                    </span>
                    </small>
                    </MDBCol>
                </MDBRow>
            </Fragment>
        );
    }
}

export default CardContent;